html, body {
    font-family: $font-style;
    line-height: 1.5;
    font-size: 16px;
    h1, h2, h3, h4, h5, h6 {
        line-height: 1.2;
        font-weight: bold;
    }
    .main-title {
        font-size: 48px;
        text-align: center;
        @include pad(){
            font-size: 32px;
        } 
        @include mobile(){
            font-size: 24px;
        }
    }
}