// faq-header
.faq-header {
    background-image: url(../img/72.png);
    height: 304px;
    .faq-header-title {
        font-size: 64px;
        line-height: 1.5;
        margin-right: 10px;
    }
}

// faq-header rwd
@include pad() {
    .faq-header {
        .faq-header-title {
            font-size: 48px;
            margin-right: 16px;
        }
        .question-icon{
            width: 48px;
            height: 48px;
        }
    }
}
@include mobile() {
   .faq-header {
        height: 143px;
        .faq-header-title {
            font-size: 28px;
            margin-right: 8px;
        }
        .question-icon{
            width: 24px;
            height: 24px;
        }
    }
}

// faq-section
.faq-section {
    padding-top: 88px;
    padding-bottom: 156px;
    .faq-section-title {
        font-size: 32px;
        margin-bottom: 16px;
    }
    .faq-card-item {
        border: 1px solid #000;
    }
    .faq-card-header,
    .faq-card-body {
        padding: 22px 40px 22px 38px;
    }
    .faq-card-header {
        .faq-card-title {
            font-size: 20px;
        }
    }
    .faq-card-body {
        background: #f2f2f2; 
        border-top: 1px solid #000;
    }
    &:not(.active) .up{
        display: none;
    }
    &.active .down {
        display: none;
    }
}

// faq-section rwd
@include pad() {
    .faq-section{ 
        padding-top: 40px;
        padding-bottom: 104px;
        .faq-section-title {
            font-size: 28px;
        }   
        .faq-card-header,
        .faq-card-body {
            padding: 16px 24px;
        }
        .faq-card-header {
            font-size: 16px;
        }
        .faq-card-body {
            font-size: 14px;
        }
    }
}

@include mobile() {
    .faq-section{ 
        padding-top: 24px;
        padding-bottom: 56px;
        .faq-section-title {
            font-size: 20px;
        }
        .faq-card-body {
            padding: 16px; 
        }
    }
}