.text-white {
    color: #fff;
}
.text-black {
    color: #000;
}
.text-lightBlack {
    color: #212529;
}
.text-gray {
    color: #707070;
}
.text-darkRed {
    color: #650300;
}
.text-lightRed {
    color: #AA0601;
}

