/* margin設定 */
.mb-8 {
    margin-bottom: 8px;
}
.mb-16 {
    margin-bottom: 16px;
}
.mb-24 {
    margin-bottom: 24px;
}
.mb-32 {
    margin-bottom: 32px;
}
.mb-40 {
    margin-bottom: 40px;
}
.mb-44 {
    margin-bottom: 44px;
}
.mb-60 {
    margin-bottom: 60px;
}
.mr-8 {
    margin-right: 8px;
}
.mr-24 {
    margin-right: 24px;
}

/* 共用class */
.d-flex {
    display: flex;
}
.justify-between {
    justify-content: space-between;
}
.justify-end {
    justify-content: flex-end;
}
.justify-center {
    justify-content: center;
}
.align-center {
    align-items: center;
}
.align-baseline {
    align-items: baseline;
}
.align-start {
    align-items: flex-start;
}
.align-end {
    align-items: flex-end;
}
.flex-wrap {
    flex-wrap: wrap;
}
.row-reverse {
    flex-direction: row-reverse;
}
.flex-column {
    flex-direction: column;
}
.bg-cover {
    background-size: cover;
    background-position: center center ;
    background-repeat: no-repeat;
}
.h-100 {
    height: 100%;
}

// container
.container {
    max-width: 1224px;
    padding: 0 12px;
    margin: 0 auto;
}