.footer {
    padding-top: 48px;
    padding-bottom: 39px;
    background: #AA0601;
    .footer-menu-item {
        margin-right: 48px;
    }
    .footer-social-icon {
        margin-right: 32px;
        &:last-child {
            margin-right: 0;
        }
    }
    .footer-contact-tel-icon {
        margin-right: 23px;
    }
    .footer-contact-mail-icon {
        margin-right: 12px;
    }
    .footer-tel,
    .footer-email {
        font-size: 36px;
    }
    .footer-contact::after {
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background: #fff;
        margin-top: 24px;
        margin-bottom: 24px;
    }
    .footer-copyright-logo {
        margin-right: 38px;
    }
    .footer-policy {
        font-size: 14px;
    }
    .footer-policy-item {
        margin-left: 62px;
    }
    .mobile-footer-logo {
        display: none;
    }
}

@include pad() {
    .footer {
        padding-top: 40px;
        padding-bottom: 40px;
        .footer-menu-item {
            margin-right: 40px;
        }
        .footer-social-icon {
            margin-right: 24px;
            width: 24px;
            height: 24px;
        }
        .footer-contact-tel-icon {
            width: 23px;
            height: 23px;
            margin-right: 12px;
        }
        .footer-contact-mail-icon {
            width: 32px;
            height: 32px;
            margin-right: 8px;
        }
        .footer-tel,
        .footer-email {
            font-size: 20px;
        }
        .footer-contact::after {
            margin-top: 27px;
        }
        .footer-copyright-logo {
            display: none;
        }
        .footer-policy-item {
            margin-left: 24px;
        }
    }
}
@include mobile(){
    .footer {
        padding-top: 24px;
        padding-bottom: 16px;
        .footer-menu {
            display: none;
        }
        .rwd-footer-contact-flex {
            display: flex;
            flex-direction: row-reverse;
            justify-content: space-between;
            align-items: flex-start;
            border-bottom: 1px solid #fff;
            padding-bottom: 24px;
            margin-bottom: 16px;
        }
        .rwd-mb0 {
            margin-bottom: 0;
        }
        .footer-contact-tel-icon {
            width: 13px;
            height: 13px;
            margin-right: 8px;
        }
        .footer-contact-mail-icon {
            width: 20px;
            height: 20px;
            margin-right: 5px;
        }
        .footer-tel,
        .footer-email {
            font-size: 16px;
        }
        .footer-contact::after {
            display: none;
        }
        .footer-copyright-policy-wrap {
            flex-direction: column;
        }
        .footer-copyright-text {
            font-size: 14px;
            margin-bottom: 8px;
        }
        .footer-policy {
            display: block;
        }
        .footer-policy-item {
            margin-bottom: 8px;
            margin-right: 0px;
        }
        .rwd-logo-flex {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .mobile-footer-logo {
            display: block;
            width: 46px;
            height: 28px;
        }
    }
}
